<template>
  <div class="hello">
    <nav>
      <ul class="nav_list clearfix">
        <li>
          <a href>慕课网首页</a>
        </li>
        <li>
          <router-link :class="{ light: sum == '2' }" to="/free"
            >免费课程</router-link
          >
        </li>
        <li>
          <router-link :class="{ light: sum == '3' }" to="/pay/0"
            >实战课程</router-link
          >
        </li>
        <li>
          <a href>就业班</a>
        </li>
        <li>
          <a href>专栏</a>
        </li>
      </ul>
      <div class="nav_r">
        <Icon @click.native="toshop" type="md-cart" />
        <router-link to="/login" v-if="!this.$store.state.user"
          >登陆</router-link
        >
        <Dropdown v-else trigger="click" style="margin-left: 20px">
          <a href="javascript:void(0)">
            {{ this.$store.state.user }}
            <Icon type="ios-arrow-down"></Icon>
          </a>
          <DropdownMenu slot="list">
            <DropdownItem @click.native="loginout">退出登陆</DropdownItem>
            <DropdownItem @click.native="info">个人中心</DropdownItem>
          </DropdownMenu>
        </Dropdown>
        <router-link to="/register" v-if="!this.$store.state.user"
          >注册</router-link
        >
      </div>
    </nav>
  </div>
</template>

<script>
export default {
  name: "HelloWorld",
  props: ["sum"],
  data() {
    return {
      data: []
    };
  },
  methods: {
    //退出登陆
    loginout() {
      this.$store.commit("LOGOUT");
      this.$router.go(0);
    },
    //去购物车
    toshop() {
      if (this.$router.history.current.path !== "/shop") {
        this.$router.push({ name: "shop" });
      }
    },
    //个人中心
    info() {
      if (
        this.$router.history.current.path !== "/user/info" &&
        sessionStorage.getItem("user") !== null
      ) {
        this.$router.push({ name: "userinfo" });
      } else if(sessionStorage.getItem("user") == null) {
        this.$store.commit("LOGOUT");
        this.$router.push({ name: "login" });
      }
    }
  }
};
</script>

<style scoped lang="scss">
.clearfix::after {
  content: "";
  display: block;
  clear: both;
}
// header {
//   background: #07111b;
// }
nav {
  width: 1386px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  line-height: 40px;
  background: #07111b;

}
nav a {
  color: #717a84;
  font-weight: bold;
}
ul,
li {
  list-style-type: none;
}
.nav_list > li {
  float: left;
  width: auto;
  padding: 0 18px;
}
.nav_list > li:nth-child(1) {
  padding-left: 0;
}
.nav_list > li > a:hover {
  color: white;
}
.light {
  color: white;
}
.nav_r > a {
  padding: 0 18px;
}
.nav_r > i {
  font-size: 20px;
  padding-right: 18px;
}
</style>
