<style scoped>
header {
  background: #07111b;
}
nav {
  position: relative;
  padding: 24px 0;
  background: url("https://img1.sycdn.imooc.com/szimg/5de8adda094a654840000800.jpg")
    no-repeat 0 0/100%;
}
.nav_top {
  text-align: center;
  color: white;
  width: 1152px;
  margin: 0 auto 56px;
  padding-top: 24px;
}
.nav_top > p {
  color: rgba(255, 255, 255, 0.6);
  display: flex;
  justify-content: space-between;
  font-size: 12px;
  padding: 16px 150px;
}
.nav_top > h1 {
  font-size: 40px;
  font-weight: 400;
  line-height: 48px;
}
.nav_top > h2 {
  font-size: 16px;
  line-height: 24px;
  font-weight: 300;
  margin: 8px 0;
}
.nav_center {
  width: 1152px;
  position: absolute;
  left: 50%;
  top: 80%;
  transform: translateX(-50%);
  display: flex;
  padding: 32px 40px;
  justify-content: space-between;
  background: white;
  border-radius: 18px;
  box-shadow: 0 8px 16px 0 rgba(28, 31, 33, 0.1);
}
.nav_center > p {
  font-size: 32px;
  color: red;
}

.nav_center > p > span:nth-child(2) {
  color: #545c63;
  margin-left: 30px;
}
.btn > button {
  padding: 12px 48px;
  outline: none;
  border: none;
  background: #f01414;
  font-size: 16px;
  color: white;
  border-radius: 24px;
}
.btn > button:nth-child(2) {
  background: rgba(240, 20, 20, 0.1);
  font-size: 16px;
  color: #f20d0d;
  margin-left: 20px;
}
main {
  padding-top: 64px;
}
.main_nav {
  padding: 24px 16px;
  text-align: center;
}
.main_center {
  display: flex;
  width: 1152px;
  margin: 36px auto 0;
  border-radius: 20px;
  padding: 64px;
  background: url("https://img.mukewang.com/szimg/5de8adf408c24a1809000500.jpg")
    no-repeat 0 0/100%;
}
.left {
  padding-right: 200px;
  border-right: 2px solid rgba(255, 255, 255, 0.4);
  width: 680px;
  font-size: 14px;
  letter-spacing: 1px;
  color: white;
  line-height: 24px;
}
.title {
  font-size: 24px;
  font-weight: bold;
  line-height: 32px;
}
.right {
  font-size: 16px;
  color: #fff;
  padding: 0px 24px;
  margin: 0 auto;
}
.right > img {
  margin-bottom: 24px;
  border-radius: 50%;
  border: 4px solid white;
}
.ifpage {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 50px;
  font-weight: 700;
  background: linear-gradient(to right, red, blue);
  background-clip: text;
  color: transparent;
}
</style>
<template>
  <div>
    <div v-if="ifpage">
      <header>
        <navs />
      </header>
      <nav>
        <div class="nav_top">
          <h1>{{ content.title }}</h1>
          <h2>{{ content.introduce }}</h2>
        </div>
        <div class="nav_center">
          <p>
            <span>{{ content.money }}元 </span>
            <span>{{ content.watch }}人观看</span>
          </p>
          <div class="btn">
            <button>立即购买</button>
            <button @click="toshop">加入购物车</button>
          </div>
        </div>
      </nav>
      <main>
        <div class="main_center">
          <div class="left">
            <div class="title">{{ content.title }}</div>
            <div class="content">
              {{ content.title }}{{ content.title }}{{ content.title
              }}{{ content.title }}{{ content.title }}{{ content.title }}
            </div>
          </div>
          <div class="right">
            <img
              src="https://img.mukewang.com/user/587dd3db0001488708521136-100-100.jpg"
              alt
            />
            <p>{{ content.author }}</p>
          </div>
        </div>
      </main>
    </div>
    <div v-else class="ifpage">
      <h1>页面不存在</h1>
    </div>
  </div>
</template>
<script>
import navs from "../components/nav";

export default {
  components: {
    navs
  },
  data() {
    return {
      pageid: this.$route.params.id,
      content: [],
      ifpage: true
    };
  },
  methods: {
    page() {
      this.$axios({
        url: "http://127.0.0.1:3000/api/page",
        params: {
          pageid: this.$router.history.current.params.id
        }
      }).then(res => {
        if (res.data.data != undefined && res.data.data != null) {
          this.content = res.data.data;
          this.ifpage = true;
        } else {
          this.ifpage = false;
        }
      });
    },
    toshop() {
      if (window.sessionStorage.getItem("user") == null) {
        return alert("请先登录");
      }
      this.$axios({
        method: "POST",
        url: "http://127.0.0.1:3000/api/toshop",
        params: {
          pid: this.content.id,
          title: this.content.title,
          pic: this.content.cover_src,
          money: this.content.money,
          uid: JSON.parse(window.sessionStorage.getItem("user")).id
        }
      }).then(res => {
        if (res.data.data.affectedRows == 1) {
          alert("成功加入购物车");
        } else if (res.data.like.msg) {
          alert("已经加入不要在点");
        } else {
          alert("加入失败");
        }
      });
    }
  },
  mounted() {
    this.page();
  },
  watch: {
    "$route.path"() {
      this.page();
    }
  }
};
</script>
